// MAIN //

/**
* Revives a JSON-serialized `Buffer`.
*
* @param {string} key - key
* @param {*} value - value
* @returns {(*|Buffer)} value or Buffer
*
* @example
* var parseJSON = require( '@stdlib/utils/parse-json' );
*
* var str = '{"type":"Buffer","data":[5,3]}';
*
* var buf = parseJSON( str, reviver );
* // returns <Buffer>[ 5, 3 ]
*/
function reviver( key, value ) {
	if (
		value &&
		value.type === 'Buffer' &&
		isArray( value.data )
	) {
		return array2buffer( value.data );
	}
	return value;
}


// EXPORTS //
export default reviver;
